.stars {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 0;
  margin: 0;

  &__star {
    background-image: url(../images/star-active.svg);
    background-repeat: no-repeat;
    background-position: center;
    display: inline-block;

    width: 16px;
    height: 16px;
    margin-right: 4px;

    &:nth-child(n + 5) {
      background-image: url(../images/star.svg);
    }

    &:last-of-type {
      margin-right: 0;
    }
  }

  &__grade {
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
    color: $main-color;
  }
}
