.header {
  box-sizing: border-box;
  text-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__link {
  align-content: center;
  align-items: center;
  display: flex;
}

.header__nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__nav-item {
  color: #0000;
  margin-left: 20px;
}

.header__nav-item:first-child {
  margin-left: 0;
}

.header__nav-link {
  color: #060b35;
  align-content: center;
  align-items: center;
  height: 60px;
  line-height: 14.06px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.header__nav-link:hover, .is-active {
  color: #00acdc;
}

.is-active:before {
  margin-left: 0;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.catalog {
  grid-template-rows: repeat(2, 408px);
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  justify-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

.catalog__card {
  box-sizing: border-box;
  color: #060b35;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  font-size: 12px;
  line-height: 18px;
  transition: all .3s;
  display: flex;
}

.catalog__card:hover {
  transform: scale(1.2);
}

.catalog__card-imac {
  justify-content: center;
  align-self: center;
  width: 160px;
  height: 134px;
}

.catalog__card-title {
  margin-top: 40px;
  margin-bottom: 0;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
}

.catalog__card:hover .catalog__card-title {
  color: #34568b;
}

.catalog__card-product-code {
  color: #616070;
  margin-top: 4px;
  margin-bottom: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.catalog__card-rate {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  margin-bottom: 24px;
  display: flex;
}

.catalog__card-rate-reviews {
  color: #616070;
  margin: 0;
  padding-top: 1px;
  font-size: 10px;
  line-height: 14px;
}

.catalog__card-price {
  flex-direction: corowlumn;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.catalog__card-price-text {
  color: #616070;
  margin: 0;
  padding: 0;
  font-weight: 400;
}

.catalog__card-price-cost {
  color: #060b35;
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-weight: 700;
}

.catalog__card-button {
  box-sizing: border-box;
  text-transform: uppercase;
  cursor: pointer;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .4s;
  display: flex;
}

.catalog__card-button:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding-left: 0;
  display: flex;
}

.stars__star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: inline-block;
}

.stars__star:nth-child(n+5) {
  background-image: url("star.822d561e.svg");
}

.stars__star:last-of-type {
  margin-right: 0;
}

.stars__grade {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

@media (width <= 488px) {
  .catalog {
    grid-template-columns: repeat(1, 200px);
  }
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  margin: 0;
}
